import darken from 'polished/lib/color/darken';
import styled from 'styled-components';

export interface IProps {
  /** Color of the link */
  color?: string;
  /**
   * Show underline
   * @default true;
   */
  underline?: boolean;
  /**
   * Light theme
   * @default false
   */
  light?: boolean;
  /** Theme (will be passed automatically in most cases) */
  theme?: {
    mutedColor: string;
    primaryColor: string;
  };
}

/**
 * @render react
 * @name Link
 * @description Customizable link with theme support
 * @example
 * <Link>Click here</Link>
 */
export const Link = styled('a')<IProps>`
  color: ${props =>
    props.color
      ? props.color
      : props.light
      ? props.theme && props.theme.mutedColor
      : props.theme && props.theme.primaryColor};
  cursor: pointer;

  svg {
    fill: currentColor;
  }

  @media (hover: hover) {
    &:hover {
      color: ${props =>
        props.color
          ? darken(0.25, props.color)
          : props.light
          ? darken(0.25, props.theme.mutedColor)
          : darken(0.15, props.theme.primaryColor)};
      text-decoration: ${props => (!props.underline ? 'none' : 'underline')};
    }
  }
`;

Link.defaultProps = {
  light: false,
  theme: {
    mutedColor: '#8492a6',
    primaryColor: '#108ee9',
  },
  underline: true,
};

export default Link;
