import * as React from 'react';
import styled from 'styled-components';

/** Styled close button for Alert component */
const AlertCloseButton = styled.button`
  background: transparent;
  border: 0;
  color: inherit;
  cursor: pointer;
  font-size: 1.2rem;
  opacity: 0.7;
  position: absolute;
  right: 0;
  top: 0.2rem;
  transition: all 0.5s;

  &:hover {
    opacity: 1;
  }

  > span {
    cursor: pointer;
  }
`;

// TODO: 2. mb we can provide an API with timeout param and when this timout will be expired after
// render we can destroy this component itself. For example on close will also be invoked in this case so upper
// logical component will know when alert had been destroyed to change state etc.
export interface IProps {
  /**
   * Alert type
   * @default 'info'
   */
  type?: 'info' | 'success' | 'warning' | 'error';
  /**
   * On close event. If presented 'X' icon will be shown
   * @default undefined
   */
  onClose?: () => void;

  children: any;
  className?: string;
  theme?: any;
}

interface IAlertState {
  /**
   * Determines if Alert is closed or not.
   * @default false
   */
  closed: boolean;
}

/**
 * @render react
 * @name Alert
 * @description Displays important alert messages.
 * @example
 * <Alert type="default">Default Alert</Alert>
 */
class Alert extends React.Component<IProps, IAlertState> {
  public static defaultProps: Partial<IProps> = {
    type: 'info',
  };

  public state = {
    closed: false,
  };

  private onClose = () => {
    this.setState({ closed: true });
    if (typeof this.props.onClose === 'function') {
      return this.props.onClose();
    }
  };

  public render() {
    if (this.state.closed) {
      return null;
    }

    const { children, onClose, ...props } = this.props;

    return (
      <div className={this.props.className} {...props}>
        {children}
        {/* Add close button only in case if onClose prop is added and is function */}
        {typeof onClose === 'function' && (
          <AlertCloseButton type="button" aria-label="Close" onClick={this.onClose}>
            <span aria-hidden="true">&times;</span>
          </AlertCloseButton>
        )}
      </div>
    );
  }
}

const AlertStyled = styled(Alert)<IProps>`
  display: block;
  width: 100%;
  font-size: 0.95rem;
  margin-bottom: 1rem;
  padding: 0.65rem 1.25rem;
  position: relative;
  border: 1px solid transparent;
  border-radius: 0.35rem;
  background: ${(props) => props.theme.alerts[props.type || 'info'].background};
  color: ${(props) => props.theme.alerts[props.type || 'info'].color};
`;

AlertStyled.defaultProps = {
  theme: {
    alerts: {
      error: {
        background: '#fef0f0',
        color: '#f56c6c',
      },
      info: {
        background: '#f4f4f5',
        color: '#909399',
      },
      success: {
        background: '#eff9e9',
        color: '#67c23a',
      },
      warning: {
        background: '#fdf6ec',
        color: '#e6a23c',
      },
    },
  },
};

/** @component */
export default AlertStyled;
