/**
 * get initials from string(full name)
 * @name getInitials
 * @param {string} name - name to get initials
 * @returns {string} two first letters from 2 words for english and 1 for non-english
 * @example
 * getInitials('Gomer Simpson') // => GS
 * getInitials('Гомер Симпсон') // => Г
 * getInitials('Gomer') // => G
 */
export function getInitials(name: string): string {
  const nameArr = name.split(' ').slice(0, 2);
  const formatted = nameArr.map(word => word[0]).join('');
  // display only one letter for non-english because sybols might be wider than expected
  if (!formatted.match(/[a-zA-z1-9]/)) {
    return formatted[0];
  }
  return formatted;
}

/**
 * Convert string array('[1,2,3]') to array([1,2,3])
 * @name stringToArray
 * @param {string} item - parameter to convert
 * @return {array} - returns converted array if argument is valid, otherwise empty array
 * @example
 * stringToArray('[1,2,3]') // => [1,2,3]
 * stringToArray('') // => []
 */
export function stringToArray(item: any): any[] {
  try {
    const emotesArr = JSON.parse(item);
    if (Array.isArray(emotesArr)) {
      return emotesArr;
    }
    return [];
  } catch (err) {
    return [];
  }
}

/**
 * Check if string contains offensive words
 * @param {string} strToCheck - string you want to check
 * @returns {boolean}
 */
// tslint:disable-next-line
const offensiveWords: RegExp = /anus|ash0le|asshole|troll|hurtz|azzhole|bastard|biatch|bitch|blowjob|blow job|butthole|c0ck|cock|dick|dildo|dild0|dildos|faggot|fuck|n1gr|nigger|orgasm|pen1s|penis|pussy|sadist|sh!t|sh1t|vag1na|vagina|whore|blowjob|b!tch|c0ck|fatass|masturbate|nigga|chuj|p0rn|vagiina/gi;
export const isOffensive = (strToCheck: string): boolean => offensiveWords.test(strToCheck);

/**
 * Check if name doesn't containt any forbidden words
 * @param {string} name - name to check
 * @returns {boolean} - `true` if forbidden word found and `false` if not
 */
const nameRegex: RegExp = /[~`!#$%@\^&*+=\-\[\]\\';,/{}|\\":<>\?]|www.|[0-9]/;
export const isValidName = (name: string): boolean => !!name.trim() && !nameRegex.test(name);

/**
 * Check if email is valid
 * @param {string} email - email to check
 * @returns {boolean} - `true` if email is valid and `false` if not
 */
const emailRegex: RegExp = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/;
export const isValidEmail = (email: string): boolean =>
  !!email.trim() && emailRegex.test(email) && email.indexOf('www.') === -1;

/**
 * Remove special chars from the string, make it good looking for URL
 * @param text
 */
export function slugify(text: string) {
  const slug = text
    .toString()
    .toLowerCase()
    .replace(/\s+/g, '-') // Replace spaces with -
    .replace(/[^\w\-]+/g, '') // Remove all non-word chars
    .replace(/\-\-+/g, '-') // Replace multiple - with single -
    .replace(/^-+/, '') // Trim - from start of text
    .replace(/-+$/, ''); // Trim - from end of text
  return slug || '-';
}

/**
 * Serialize object
 * @param {object} obj - object which needs to be serialized
 * @returns {string} - Example: id=123&name=vuukle
 */
export const serialize = (obj: { [key: string]: any }): string =>
  Object.keys(obj)
    .filter(key => obj[key] !== undefined && obj[key] !== null)
    .map(key => `${key}=${encodeURIComponent(obj[key])}`)
    .join('&');

/**
 * @name hashCode
 * @param {string} s - string from which to generate hash
 * @returns {number} - hash value
 */
export function hashCode(s: string): number {
  const str = String(s);
  let hash = 0;
  let char;
  if (str.trim().length === 0) {
    return hash;
  }
  for (let i = 0; i < str.length; i++) {
    char = str.charCodeAt(i);
    hash = (hash << 5) - hash + char; // tslint:disable-line
    // Convert to 32bit integer
    hash &= hash; // tslint:disable-line
  }
  return Math.abs(hash);
}

/**
 * @name makeSafeHTML
 * @description replaces all tags inside text to be an HTML string except allowed tags in our comments
 * @param {string} s - HTML string where to convert unsafe HTML tags to string
 */
export function makeSafeHTML(s: string): string {
  return s.replace(
    /(?!(<\/?b>|<\/?br\/?>|<\/?u>|<\/?i>|<\/?pre>|<\/?blockquote>|<\/?ul>|<\/?li>|<\/?p>|<\/?span>|<img src=["|'].+["|']>))<\/?([a-z]+)([^>]*>)?/gi,
    '&lt;$2&gt;'
  );
}

/**
 * @name removeHTMLTags
 * @description removes all html tags from the string
 * @param {string} s - string from which to remove HTML tags
 * @return {string} String with remove HTML tags
 */
export function removeHTMLTags(s: string): string {
  const div = document.createElement('div');
  div.innerHTML = s;
  return div.innerText;
}

/**
 * @name removeBBcode
 * @description Clear string from BBcode syntax
 * @param {string} s - string from which to remove BBcode syntax
 * @return {string} - string without BBcode
 */
export function removeBBcode(s: string): string {
  return s.replace(/\[\/?(?:b|i|u|url|quote|code|img|color|size)*?.*?\]/gim, '');
}

/**
 * @name getTextFromHTML
 * @description Get text from HTML string with HTML tags and HTML character codes
 * @param {string} HTMLString - HTML string from which we need to get text
 * @return {string} - string from rendered html
 */
export function getTextFromHTML(HTMLString: string): string {
  const para = document.createElement('p');
  para.innerHTML = HTMLString;
  return para.innerText.trim();
}

/**
 * @name truncateString
 * @param {string} text - text to truncate
 * @param {string} maxLength - max allowed length
 * @return {string} truncated string
 */
export function truncateString(text: string, maxLength: number = 70): string {
  if (text.length > maxLength) {
    const trimmedString = text.trim().substr(0, maxLength);
    // Find if latest word has not been truncated. If yes then remove it too (in case if it's not only one long word)
    if (text.split(' ').length > 1 && text.split(' ').indexOf(trimmedString.split(' ').pop() as string) < 0) {
      return trimmedString.substr(0, Math.min(trimmedString.length, trimmedString.lastIndexOf(' '))).trim();
    }
    return trimmedString + '...';
  }
  return text;
}

/**
 * @name decodeString
 * @description Tries to decode string if possible, if not then returns same value as argument was
 * @param {string} str - string to decode
 * @returns {string} decoded string or same string if not possible to decode
 */
export function decodeString(str: string): string {
  try {
    return decodeURIComponent(str);
  } catch (e) {
    return str;
  }
}
