import * as React from 'react';
import styled, { css } from 'styled-components';
import MenuItem, { IMenuItemProps } from './MenuItem';

export interface IProps {
  className?: string;
  children: React.ReactElement<any> | Array<React.ReactElement<any> | React.Component | string | undefined> | string;
  style?: React.CSSProperties;
}

const darkStyles = css`
  background: #30404d;
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 0 0 rgba(16, 22, 26, 0), 0 1px 1px rgba(16, 22, 26, 0.4);
`;

const lightStyles = css`
  background: #fff;
  box-shadow: 0 0.05rem 0.2rem rgba(69, 77, 93, 0.3);
`;

const List = styled.ul`
  ${props => (props.theme.isDark ? darkStyles : lightStyles)};
  border-radius: 0.1rem;
  list-style: none;
  margin: 0;
  min-width: 140px;
  padding: 0.4rem;
  transform: translateY(0.2rem);
  z-index: 300;
`;

export class Menu extends React.Component<IProps, any> {
  public static Item: React.FunctionComponent<IMenuItemProps> = MenuItem;

  public render() {
    const { className, children, ...props } = this.props;
    return (
      <List className={`v-menu ${className}`} {...props}>
        {children}
      </List>
    );
  }
}

export default Menu;
