/**
 * format number to K formatted: 1000 becames 1K
 * @name kFormatter
 * @param {number} num - number to convert
 * @returns {number | string} returns 0 in case if it's NaN,
 * returns number if value is less or equal to 999,
 * returns string if value is more than 999 > '1.0K'
 * @example
 * kFormatter(100) // => 100
 * kFormatter(1111) // => '1.1K'
 * kFormatter(1234, 2) // => '1.23K'
 */
export function kFormatter(num: number, digitsAfterDecimal: number = 1): string | number {
  let numb = num;
  if (typeof num === 'string') {
    numb = parseInt(num, 0);
  }

  if (Number.isNaN(numb) || typeof numb !== 'number') {
    return 0;
  }

  if (num > 999) {
    const kForm = num / 1000;
    return kForm % 1 > 0 ? `${kForm.toFixed(digitsAfterDecimal)}K` : `${kForm}K`;
  }
  return num;
}

export default {
  kFormatter,
};
