import * as React from 'react';
import styled, { css } from 'styled-components';

import getLuminance from 'polished/lib/color/getLuminance';

interface IStyledProps {
  className?: string;
  href?: string;
  active?: boolean;
  children: string | React.ReactElement<any>;
  target?: '_blank' | '_self' | '_parent' | '_top';
}
export interface IMenuItemProps extends IStyledProps {
  onClick?: (e: React.MouseEvent<HTMLAnchorElement>) => void;
}

const getActiveStyles = (color: string) => {
  return css`
    background: ${color};
    color: ${getLuminance(color) < 0.2 ? '#000' : '#fff'};
  `;
};

const Item = styled.li<IStyledProps>`
  margin-top: 0;
  padding: 0 0.5rem;
  text-decoration: none;
  user-select: none;
  border-radius: 0.15rem;
  > a,
  span {
    color: inherit;
    display: block;
    margin: 0 -0.5rem;
    padding: 0.3rem 0.5rem;
    text-decoration: none;
    background: ${props => (props.active ? props.theme.primaryColor : 'transparent')};
    cursor: pointer;
  }

  ${props => props.active && getActiveStyles(props.theme.primaryColor)};

  &:hover,
  &:active,
  &:focus {
    ${props => getActiveStyles(props.theme.primaryColor)};
    a,
    span {
      color: inherit;
      text-decoration: none;
    }
  }
`;

export const MenuItem: React.FunctionComponent<IMenuItemProps> = ({
  children,
  className,
  href = '#',
  onClick,
  target = '_parent',
  ...props
}) => (
  <Item className={className} {...props}>
    {typeof onClick === 'function' ? (
      <span role="button" onClick={onClick}>
        {children}
      </span>
    ) : (
      <a href={href} target={target}>
        {children}
      </a>
    )}
  </Item>
);

export default MenuItem;
