import * as React from 'react';
import styled from 'styled-components';

import Loader from '@vuukle/loader';

export interface IProps {
  disabled?: boolean;
  onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
  loading?: boolean;
  children: Element | string | JSX.Element | Array<string | JSX.Element | Element>;
  className?: string;
  style?: React.CSSProperties;
  active?: boolean;
  /**
   * Button appearance
   * @default default
   */
  type?: 'primary' | 'subtle' | 'link';
  htmlType?: 'button' | 'reset' | 'submit' | undefined;
  sm?: boolean;
}

// TODO: document me!
const Button = ({ loading, children, disabled, htmlType = 'button', className, onClick, style }: IProps) => (
  <button className={className} type={htmlType} disabled={disabled || loading} onClick={onClick} style={style}>
    {loading && <Loader display="inline-block" margin="0 5px" />}
    {typeof children === 'string' ? <span>{children}</span> : children}
  </button>
);

const StyledButton = styled(Button)<IProps>`
  width: auto;
  max-width: 100%;
  font-weight: 500;
  background: ${(props) => props.theme.buttons[props.type || 'primary'].background};
  color: ${(props) => props.theme.buttons[props.type || 'primary'].color};
  border-color: transparent;
  white-space: nowrap;
  line-height: 1.5;
  font-size: ${(props) => (props.sm ? '13px' : '14px')};
  user-select: none;
  border-image: initial;
  padding: ${(props) => (props.sm ? '2px 6px' : '5px 12px')};
  border-radius: 4px;
  transition: all 0.2s ease;
  cursor: pointer;

  &:focus,
  &:active {
    outline: none;
  }

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }

  svg {
    fill: currentColor;
    margin-right: 2px;
  }

  > svg,
  > span,
  > div {
    vertical-align: middle;
  }

  ${(props) =>
    props.active &&
    `
    background: ${props.theme.buttons[props.type || 'primary'].hoverBackground};
    color: ${props.theme.buttons[props.type || 'primary'].hoverColor};
  `};

  /** States Styles */
  &:active {
    &:not([disabled]) {
      background: ${(props) => props.theme.buttons[props.type || 'primary'].hoverBackground};
      color: ${(props) => props.theme.buttons[props.type || 'primary'].hoverColor};
    }
  }

  @media (pointer: fine) {
    &:hover,
    &:focus {
      &:not([disabled]) {
        background: ${(props) => props.theme.buttons[props.type || 'primary'].hoverBackground};
        color: ${(props) => props.theme.buttons[props.type || 'primary'].hoverColor};
      }
    }
  }
`;

export default StyledButton;
