import * as React from 'react';
import styled from 'styled-components';

const Icon = styled.span`
  box-sizing: border-box;
  display: inline-block;
  font-size: inherit;
  font-style: normal;
  height: 1em;
  position: relative;
  text-indent: -9999px;
  vertical-align: middle;
  width: 1em;
  &:after,
  &:before {
    display: block;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }
`;

/** Caret */
const Caret = styled((props) => <Icon {...props} />)`
  &:before {
    border-left: 0.3em solid transparent;
    border-right: 0.3em solid transparent;
    border-top: 0.3em solid currentColor;
    content: '';
    height: 0;
    transform: translate(-50%, -25%);
    width: 0;
  }
`;

/** More icons */
const MoreBase = styled((props) => <Icon {...props} />)`
  &:before {
    background: currentColor;
    border-radius: 50%;
    box-shadow: -0.4em 0, 0.4em 0;
    content: '';
    height: 3px;
    width: 3px;
  }
`;

const MoreHorizontal = MoreBase;
const MoreVertical = styled((props) => <MoreBase {...props} />)`
  &:before {
    box-shadow: 0 -0.4em, 0 0.4em;
  }
`;

export { MoreHorizontal, MoreVertical, Caret };
