import styled, { css, keyframes } from 'styled-components';
import { IProps } from './Dropdown';

const slideDown = keyframes`
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
`;

const slideTop = keyframes`
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
`;

interface IStyledProps extends Partial<IProps> {
  opened: boolean;
}

export const StyledDropdown = styled.div<IStyledProps>`
  cursor: pointer;
  display: inline-block;
  position: relative;
  /** @group menu **/
  .v-menu {
    display: ${(props) => (props.opened ? 'block' : 'none')};
    max-height: 50vh;
    overflow-y: auto;
    position: absolute;
    ${(props) => (props.align === 'right' ? `right: 0;` : `left: 0;`)};
    ${(props) =>
      props.position === 'top'
        ? css`
            animation: ${slideTop} 0.15s ease 1;
            bottom: 35px;
            top: auto;
          `
        : css`
            animation: ${slideDown} 0.15s ease 1;
            bottom: auto;
            top: 100%;
          `};
  }
`;
