import * as React from 'react';
import styled, { css } from 'styled-components';

import { getInitials, hashCode } from '@vuukle/utils';

export interface IProps {
  /**
   * Image URL
   * @default ''
   */
  src?: string | null;
  /**
   * User name. Will be visible in case if `src` is not added or not valid
   * @default '?'
   */
  name?: string;
  /**
   * Avatar size. You can use any measurement value
   * @default 40px
   */
  size?: string;
  /**
   * The value used for the hash function.
   * The name is used as the hashValue by default.
   * When dealing with anonymous users you should use the id instead.
   * @default name
   */
  hash?: string;

  onClick?: (e: any) => void;

  className?: string;
  theme?: {
    mutedColor: string;
  };
}

/**
 * @render react
 * @name Avatar
 * @description Used to display user profile picture or publisher logo
 * @example
 * <Avatar name="John Smith" size="36px"></Avatar>
 */
export const Avatar: React.FunctionComponent<IProps> = ({
  className,
  name = '?',
  src = '',
  size = '40px',
  children,
  ...props
}) => (
  <figure data-initial={getInitials(name || '?')} className={className} {...props}>
    {typeof src === 'string' && src.length > 0 && <img src={src} alt="" />}
    {children}
  </figure>
);

Avatar.defaultProps = {
  theme: {
    mutedColor: '#ccc',
  },
};

const colors = [
  // neural
  { color: '#425A70', background: '#E4E7EB' },
  // blue
  { color: '#084b8a', background: '#ddebf7' },
  // red
  { color: '#bf0e08', background: '#fae2e2' },
  // orange
  { color: '#95591e', background: '#fae3cd' },
  // yellow
  { color: '#7e6514', background: '#fbe6a2' },
  // green
  { color: '#00783e', background: '#d4eee2' },
  // teal
  { color: '#007489', background: '#d2eef3' },
  // purple
  { color: '#37248f', background: '#eae7f8' },
];

/** Avatar with styles */
const StyledAvatar = styled(Avatar)<IProps>`
  ${props => {
    if (props.hash) {
      return colors[hashCode(props.hash) % colors.length];
    }
    if (props.name) {
      return colors[hashCode(props.name) % colors.length];
    }
    return { color: '#084b8a', background: '#ddebf7' };
  }};
  font-size: 0.8rem;
  height: ${props => props.size || '40px'};
  width: ${props => props.size || '40px'};
  /* box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3); */
  border-radius: 50%;
  display: inline-block;
  font-weight: 300;
  line-height: 40px;
  margin: 0;
  position: relative;
  vertical-align: middle;

  img {
    border-radius: 50%;
    height: 100%;
    position: relative;
    width: 100%;
    z-index: 1 !important;
    object-fit: cover;
  }

  &[data-initial]::before {
    font-weight: 500;
    font-size: ${props => `${Math.round(parseInt(props.size || '40px', 10) / 3 || 15)}px`};
    color: currentColor;
    content: attr(data-initial);
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 0;
    text-transform: uppercase;
  }

  ${props => props.onClick && clickableStyles};
`;

const clickableStyles = css`
  cursor: pointer;
  transition: filter 200ms linear;
  &:hover {
    filter: brightness(90%);
  }
`;

export default StyledAvatar;
