import { serialize } from './string';

/**
 * @name makeRequest
 * @description generates request function based on params
 * @param {'GET' | 'POST' | 'DELETE' | 'PUT'} method - API request method - @default 'GET'
 * @param {string} url - Request URL
 * @param {object} data - request parameters
 * @param {string} contentType - request header Content-Type
 * @param {string | undefined} authToken @default undefined - token cookie we save in records for authenticated users
 * @param {boolean} withCredentials - use withCredentials option for a request
 */
export const makeRequest = (
  method: 'GET' | 'POST' | 'DELETE' | 'PUT',
  url: string,
  data: object | string | FormData = {},
  contentType: string | undefined = data instanceof FormData ? '' : 'application/json',
  authToken: string | undefined | null = undefined,
  withCredentials: boolean = false
): Promise<any> =>
  new Promise((resolve, reject) => {
    const xhr: XMLHttpRequest = new XMLHttpRequest();
    const reqUrl =
      method === 'GET' ? `${url}?${typeof data === 'object' && data !== null ? serialize(data) : {}}` : url;

    xhr.open(method, reqUrl, true /* Async */);

    /** Add Content-Type header if it's presented */
    if (contentType) {
      xhr.setRequestHeader('Content-Type', contentType);
    }

    /** Add Authorization header if request requires auth */
    if (authToken) {
      xhr.setRequestHeader('Authorization', `Bearer ${authToken}`);
    }

    if (withCredentials) {
      xhr.withCredentials = true;
    }

    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 300) {
        resolve(JSON.parse(xhr.response));
      } else {
        reject(new Error(xhr.statusText));
      }
    };
    xhr.onerror = () => reject(new Error(xhr.statusText));
    xhr.onabort = () => reject(new Error('cancelled'));

    method === 'GET' ? xhr.send() : data instanceof FormData ? xhr.send(data) : xhr.send(JSON.stringify(data));
  });

/**
 * Utility to load JS file in the document. Appends file to the head of the document
 * @param {string} src - script url
 * @param {object} attributes - attr object to add to script
 * @return {Promise<string | error>}
 */
export const loadJS = (src: string, attributes: { [key: string]: string } = {}): Promise<string | Error> =>
  new Promise((resolve, reject) => {
    /**
     * ⚠ NOTE: Scripts that are dynamically created and added to the document are async by default,
     * they don’t block rendering and execute as soon as they download, meaning they could come out in the wrong order.
     * @see https://www.html5rocks.com/en/tutorials/speed/script-loading/
     */
    const scriptTag = document.createElement('script');
    scriptTag.type = 'text/javascript';
    scriptTag.src = src;
    scriptTag.onload = () => resolve(src);
    scriptTag.onerror = reject;
    scriptTag.async = true; // Despite fact that it's async by default some publishers complain that they don't see 'async' attr. 🤷‍️
    // Set additional scrip attributes
    if (attributes instanceof Object) {
      Object.keys(attributes).forEach(key => ((scriptTag as any)[key] = attributes[key]));
    }

    try {
      document.head.appendChild(scriptTag);
    } catch (err) {
      reject(err);
    }
  });

export default makeRequest;
