import styled, { keyframes } from 'styled-components';

// keyframes returns a unique name based on a hash of the contents of the keyframes
const load8 = keyframes`
   0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
`;

export interface IProps {
  /**
   * Display as block or inline-block
   * @default block
   */
  display?: 'block' | 'inline-block';
  /**
   * Space around element
   * @default 0 auto
   */
  margin?: string;
  /**
   * Width of loader progress
   * @default 2px
   */
  borderWidth?: string;
  /**
   * Loader's size
   * @default 10px
   */
  size?: string;
  /**
   * Makes loader white. Use for non-white backgrounds
   * @default false
   */
  white?: boolean;
}

/**
 * @render react
 * @name Loader
 * @description CircleProgress component used inside buttons or similar elements to illustrate loading
 * Usage examples: demonstrate loading
 * @example
 * <Loader></Loader>
 */
const Loader = styled.div<IProps>`
  display: ${(props) => props.display || 'block'};
  vertical-align: middle;
  margin: ${(props) => props.margin || '0 auto'};
  font-size: 10px;
  position: relative;
  text-indent: -9999px;
  border: ${(props) => `${props.borderWidth || '2px'} solid ${props.white ? 'rgba(255, 255, 255, .2)' : '#E5E9F2'}`};
  border-left-color: ${(props) => props.theme.primaryColor};
  border-bottom-color: ${(props) => props.theme.primaryColor};
  transform: translateZ(0);
  animation: ${load8} 0.5s infinite linear;
  &,
  &:after {
    border-radius: 50%;
    width: ${(props) => props.size || '10px'};
    height: ${(props) => props.size || '10px'};
  }
`;

Loader.defaultProps = {
  theme: {
    primaryColor: '#108ee9',
  },
};

export default Loader;
