import Image from '@vuukle/image';
import * as React from 'react';
import styled, { css } from 'styled-components';

export interface IProps {
  /**
   * Children of the Card component
   * @default undefined
   */
  children?: any;
  /**
   * Card title
   * @default undefined
   */
  heading?: string;
  /**
   * Card description
   * @default undefined
   */
  description?: string;
  /**
   * Image URL
   * @default undefined
   */
  img?: string;
  /**
   * Card link
   * @default undefined
   */
  link?: string;
  /**
   * Card link 'rel`
   */
  linkRel?: 'noopener noreferrer' | 'noopener nofollow' | 'noopener' | 'noreferrer' | 'nofollow';
  /**
   * Article mode. Show image on top or on side of text
   * @default vertical
   */
  mode?: 'vertical' | 'horizontal';
  /**
   * Custom styles object for article card
   * @default {}
   */
  customStyles?: any;

  className?: string;
}

/**
 * @render react
 * @name Card
 * @description Integrate information in a card container
 * @example
 * <Card
 *  img="https://images.unsplash.com/photo-1466554735730-6281a8638806?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy"
 *  heading="Hello"
 *  description="It's default card. Here is the place of description"
 * />
 */
export const Card: React.StatelessComponent<IProps> = ({
  children,
  heading,
  description,
  img,
  className,
  link = '#1',
  linkRel = '',
}) => {
  return (
    <a className={className} href={link} target="_parent" rel={linkRel}>
      <div className="v-card__img">
        <Image src={img} alt={heading} />
      </div>
      <div className="v-card__content">
        <h2 className="v-card__heading">
          <span>{heading}</span>
        </h2>
        <span className="v-card__description">{description}</span>
        <div>{children}</div>
      </div>
    </a>
  );
};

const StyledCard = styled(Card)<IProps>`
  display: ${(props) => (props.mode === 'horizontal' ? 'flex' : 'block')};
  flex-direction: row;
  background-color: ${(props) => (props.theme.isDark ? '#5a5a5a' : '#fff')};
  color: ${(props) => (props.theme.isDark ? '#fff' : '#303133')};
  box-shadow: ${(props) =>
    props.theme.isDark ? '0 0 1px 0 rgba(255, 255, 255, 0.4)' : '0 0 1px 0 rgba(0, 0, 0, 0.4)'};
  border-radius: 4px;
  overflow: hidden;
  text-decoration: none;
  transition: all 0.2s;

  &:hover {
    transform: translateY(-1px);
  }

  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    .v-card__heading span {
      border-bottom: 1px solid ${(props) => props.theme.primaryColor};
    }
    color: ${(props) => (props.theme.isDark ? '#fff' : '#303133')};
  }

  > .v-card__img {
    max-height: 160px;
    ${(props) => props.mode === 'horizontal' && 'height: 100%'};
    width: 100%;
    flex: ${(props) => (props.mode === 'horizontal' ? '0 0 30%' : '1 0 100%')};
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      /* https://caniuse.com/#feat=object-fit */
      object-fit: cover;
    }
  }

  > .v-card__content {
    padding: 0.5rem;
  }

  .v-card__heading {
    word-break: break-word; /* Break word to prevent overflowing of long titles */
    font-size: 1em;
    font-weight: 600;
    margin: 0.5em 0;
    color: inherit;
    &:after {
      content: '';
      width: 40px;
      height: 1px;
      margin: 0 auto;
      background-color: #2095ff;
    }
  }

  .v-card__description {
    font-size: 0.85em;
  }

  ${(props) => props.customStyles && css(props.customStyles)}
`;

export default StyledCard;
