/**
 * @file Main basic styles we generate for widgets
 */

import darken from 'polished/lib/color/darken';
import rgba from 'polished/lib/color/rgba';
import { css } from 'styled-components';

export default (theme: Theme) => {
  return css`
    *,
    *::before,
    *::after {
      box-sizing: inherit;
    }

    html {
      box-sizing: border-box;
      font-size: 15px;
      line-height: 1.428571429;
      -webkit-tap-highlight-color: transparent;
    }

    body {
      background: transparent;
      font-family: ${theme.fontFamily};
      color: ${theme.textColor};
      font-size: 1rem;
      overflow-x: hidden;
      overflow-y: hidden;
      text-rendering: optimizeLegibility;
    }

    body.bg-dark {
      color: #fff;
    }

    a {
      color: ${theme.primaryColor};
      outline: none;
      text-decoration: none;

      &:focus {
        box-shadow: 0 0 0 0.1rem ${rgba(theme.primaryColor, 0.2)};
      }

      &:focus,
      &:active,
      &.active {
        color: ${darken(0.1, theme.primaryColor)};
        text-decoration: underline;
      }
    }

    @media (hover: hover) {
      a:hover {
        color: ${darken(0.1, theme.primaryColor)};
        text-decoration: underline;
      }
    }

    a,
    button {
      cursor: pointer;
    }

    /**
     * Styles Related For comments rendering
     */
    [contenteditable],
    .v-comment__comment {
      pre {
        background: ${theme.isDark ? '#2d2d2d' : '#f5f7f8'};
        padding: 1em;
        margin: 0.5em 0;
        overflow: auto;
        font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
        text-align: left;
        white-space: pre;
        word-spacing: normal;
        word-break: normal;
        word-wrap: normal;
        line-height: 1.5;
        tab-size: 2;
        color: ${theme.isDark ? '#ccc' : 'black'};
      }

      blockquote {
        padding-left: 10px;

        position: relative;
        margin: 5px 0;
        &:before {
          left: 0;

          content: '';
          background-color: ${theme.isDark ? 'rgba(255, 255, 255, 0.4)' : 'rgba(0, 0, 0, 0.1)'};
          width: 2px;
          position: absolute;
          top: -2.5px;
          bottom: -2.5px;
        }
      }

      p {
        margin: 2px 0;
      }
      ul,
      ol {
        padding-left: 15px;
        margin: 5px 0;
      }

      img {
        max-width: 100%;
        max-height: 480px;
        z-index: -1;
      }

      @media (max-width: 375px) {
        img {
          max-height: 250px;
        }
      }
    }
  `;
};
