import classNames from 'classnames';
import * as React from 'react';
import styled from 'styled-components';
import TabItem, { ITabItemProps } from './TabItem';

export interface ITabsProps {
  /** Default active tab name from `Tabs.Pane` component */
  active: string;
  /** Event handled on tab select */
  onSelect: (tabName: string, e: React.SyntheticEvent<any>) => void;
  /** Tabs.Pane elements inside */
  children: Array<React.ReactElement<ITabItemProps> | React.Component<ITabItemProps>>;
  className?: string;
}

interface ITabsState {
  children: React.ReactChild[];
}

/** Divide data collections which are related yet belong to different types. */
export class Tabs extends React.PureComponent<ITabsProps, ITabsState> {
  private handleTabClick = (tab: React.ReactElement<ITabItemProps>, e: React.SyntheticEvent<any>): void | boolean => {
    if (tab.props.disabled) {
      return false;
    }

    this.props.onSelect(tab.props.name, e);
  };

  private createChild = (child: React.ReactChild) => {
    // Check if child is TabItem component
    if (typeof child === 'object' && child.type === TabItem) {
      return React.cloneElement(child, {
        active: this.props.active === child.props.name,
        className: classNames(child.props.className, 'tabItem', { active: this.props.active === child.props.name }),
        onClick: (e: React.MouseEvent<HTMLButtonElement>) => {
          this.handleTabClick(child as React.ReactElement<any>, e);
        },
      });
    }
    return child;
  };

  public render() {
    return (
      <ul className={this.props.className}>
        {React.Children.map((React.Children.toArray(this.props.children) as React.ReactChild[]), this.createChild)}
      </ul>
    );
  }
}

const StyledTabs = styled(Tabs)<ITabsProps>`
  padding: 0;
  align-items: center;
  border-bottom: 0.05rem solid ${(props) => (props.theme.isDark ? '#757575' : '#e7e9ed')};
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0.2rem 0 0.15rem 0;

  .tabItem,
  .v-dropdown {
    margin-top: 0;

    > a {
      border-bottom: 0.1rem solid transparent;
      color: inherit;
      font-weight: 500;
      display: block;
      margin: 0 0.4rem 0 0;
      padding: 0.4rem 0.3rem 0.4rem 0.3rem;
      text-decoration: none;
      outline: none;
      &:hover {
        color: ${(props) => props.theme.primaryColor};
      }
    }

    &.active > a {
      color: ${(props) => props.theme.primaryColor};
      font-weight: bold;
      border-bottom-color: ${(props) => props.theme.primaryColor};
    }
  }
`;

export default StyledTabs;
