import * as React from 'react';
import styled from 'styled-components';

const SeeMore = styled.a`
  z-index: 901;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  padding: 5px 0;
  border-top: 2px solid #e7e9ee;
  display: block;
  color: #656c7a;
  &:before {
    content: ' ';
    height: 3px;
    display: block;
    box-shadow: 0 -3px 3px rgba(0, 0, 0, 0.08);
    position: relative;
    top: -5px;
  }
  &:hover {
    color: ${(props) => props.theme.primaryColor};
  }
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
    box-shadow: none;
  }
`;

export interface IProps {
  children: JSX.Element[] | JSX.Element | string;
  seeMore?: string;
}

interface IState {
  truncated: boolean;
}

export default class Truncate extends React.Component<IProps, IState> {
  public wrapperRef: HTMLParagraphElement | null;

  constructor(props: IProps) {
    super(props);
    this.state = { truncated: false };
    this.wrapperRef = null;
  }

  public static defaultProps = {
    seeMore: 'Read more',
  };

  public componentDidMount() {
    if (this.wrapperRef && this.wrapperRef.offsetHeight > 350) {
      this.setState({ truncated: true });
    }
  }

  public expandComment = (e: React.MouseEvent<HTMLAnchorElement>) => {
    e.preventDefault();
    this.setState({ truncated: false });
  };

  public render() {
    // TODO: Returns as array once preact will support fragments or array in returns
    return (
      <div>
        <p
          key="comment"
          ref={(el) => (this.wrapperRef = el)}
          style={this.state.truncated ? { maxHeight: '350px', overflow: 'hidden' } : {}}
        >
          {this.props.children}
        </p>
        {this.state.truncated && (
          <SeeMore href="#no" onClick={this.expandComment} key="seemore">
            {this.props.seeMore}
          </SeeMore>
        )}
      </div>
    );
  }
}
