import * as React from 'react';
import { Caret, MoreHorizontal, MoreVertical } from './styles';

export interface IProps {
  /**
   * Type of icon
   * @default caret
   */
  type: 'caret' | 'more-vertical' | 'more-horizontal';
  /**
   * Icon size
   * @default 1em
   */
  size?: string;
}

// TODO: document me!
export const Icon: React.FunctionComponent<IProps> = ({ type = 'caret', size, ...props }) => {
  const style: any = {};
  if (size) {
    style.width = size;
    style.height = size;
  }

  switch (type) {
    case 'more-horizontal':
      return <MoreHorizontal style={style} {...props} />;
    case 'more-vertical':
      return <MoreVertical style={style} {...props} />;
    default:
      return <Caret style={style} {...props} />;
  }
};

export default Icon;
