import darken from 'polished/lib/color/darken';
import rgba from 'polished/lib/color/rgba';
import * as React from 'react';
import styled from 'styled-components';

export interface IProps {
  className?: string;
  type?: 'text' | 'email' | 'password';
  name?: string;
  id?: string;
  onChange?: (e: React.FormEvent<HTMLInputElement>) => void;
  placeholder?: string;
  required?: boolean;
  value?: string;
  dir?: 'rtl' | 'ltr' | 'auto';
  minLength?: number;
  maxLength?: number;
  pattern?: string;
  title?: string;
  disabled?: boolean;
}

const StyledInput = styled('input')<IProps>`
  appearance: none;
  background: ${props => (props.disabled ? darken(0.1, props.theme.input.background) : props.theme.input.background)};
  background-image: none;
  border: 1px solid ${props => props.theme.input.border};
  border-radius: 3px;
  color: ${props => props.theme.textColor};
  transition: all 0.2s ease;
  display: block;
  font-size: 1em;
  line-height: 20px;
  max-width: 100%;
  outline: none;
  padding: 0.375rem 0.5rem;
  position: relative;
  width: 100%;
  cursor: ${props => (props.disabled ? 'not-allowed' : 'text')};
  &:focus {
    border-color: ${props => props.theme.primaryColor};
    box-shadow: 0 0 0 0.1rem ${props => rgba(props.theme.primaryColor, 0.2)};
  }

  &::placeholder {
    color: ${props => props.theme.mutedColor};
  }

  /* validation based on :placeholder-shown (Edge doesn't support it yet) */
  &:not(:placeholder-shown) {
    &:invalid {
      border-color: ${props => props.theme.errorColor};
      &:focus {
        box-shadow: 0 0 0 0.1rem ${props => rgba(props.theme.errorColor, 0.2)};
      }
    }
  }

  /** Firefox - fix validation highlighting instantly on display */
  &:invalid {
    box-shadow: 0 0 2px red;
  }

  &:required {
    box-shadow: none;
  }
`;

StyledInput.defaultProps = {
  theme: {
    errorColor: '#e85600',
    input: {
      background: '#fff',
      border: '#caced7',
    },
    mutedColor: '#ccc',
    primaryColor: '#108ee9',
    textColor: '#3c4858',
  },
};

// TODO: write simple docs for this file
class Input extends React.PureComponent<IProps, {}> {
  public render() {
    return <StyledInput {...this.props} />;
  }
}

export default Input;
