import * as React from 'react';
import { StyledDropdown } from './styles';

export interface IDropDownItem {
  name: string;
  className?: string;
  onClick?: (e: React.MouseEvent<HTMLAnchorElement>) => void;
  href?: string;
}

export interface IProps {
  children: any;
  /**
   * Dropdown position. If 'top' then drop up
   * @default bottom
   */
  position?: 'top' | 'bottom';
  /**
   * Dropdown menu alignment
   * @default left;
   */
  align?: 'left' | 'right';
  /**
   * Menu element which should be shown
   */
  menu: Array<React.ReactElement<any>> | React.ReactElement<any>;
  className?: string;
}

interface IState {
  displayMenu: boolean;
}

class Dropdown extends React.Component<IProps, IState> {
  private dropdownRef: React.RefObject<HTMLDivElement> = React.createRef();
  public readonly state = {
    displayMenu: false,
  };

  public showMenu = () => {
    this.setState({ displayMenu: true }, () => document.addEventListener('click', this.hideMenu));
  };

  public hideMenu = (): void => {
    this.setState({ displayMenu: false }, () => document.removeEventListener('click', this.hideMenu));
  };

  public render() {
    const { children, menu, className = '', ...rest } = this.props;

    return (
      <StyledDropdown
        className={`${className} v-dropdown`}
        ref={this.dropdownRef}
        onClick={this.showMenu}
        opened={this.state.displayMenu}
        {...rest}
      >
        {children}
        {this.state.displayMenu && this.props.menu}
      </StyledDropdown>
    );
  }
}

export default Dropdown;
