import { isMobile } from '@vuukle/utils';
import darken from 'polished/lib/color/darken';
import getLuminance from 'polished/lib/color/getLuminance';
import lighten from 'polished/lib/color/lighten';

export function generateTheme(
  theme: { darkMode: boolean; color: string | null; font: string | null },
  isArabic: boolean
): Theme {
  const mainColors = {
    errorColor: '#dc3545',
    mutedColor: '#8492a6',
    primaryColor: !theme.color || !/(^#[0-9A-F]{6}$)|(^#[0-9A-F]{3}$)/i.test(theme.color) ? '#228be6' : theme.color,
    textColor: theme.darkMode ? '#fff' : '#454d5d',
  };
  const fallbackFonts = `'Helvetica Neue', sans-serif`;
  const mainColorIsDark = getLuminance(mainColors.primaryColor) < 0.3;

  return {
    ...mainColors,
    alerts: {
      error: {
        background: '#fef0f0',
        color: '#f56c6c',
      },
      info: {
        background: '#f4f4f5',
        color: '#909399',
      },
      success: {
        background: '#eff9e9',
        color: '#67c23a',
      },
      warning: {
        background: '#fdf6ec',
        color: '#e6a23c',
      },
    },
    buttons: {
      link: {
        active: 'transparent',
        background: 'transparent',
        color: mainColors.primaryColor,
        hoverBackground: 'transparent',
        hoverColor: darken(0.1, mainColors.primaryColor),
      },
      primary: {
        active: '',
        background: mainColors.primaryColor,
        color: mainColorIsDark ? '#fff' : '#000',
        hoverBackground: mainColorIsDark
          ? lighten(0.05, mainColors.primaryColor)
          : darken(0.05, mainColors.primaryColor),
        hoverColor: mainColorIsDark ? '#fff' : '#000',
      },
      subtle: {
        active: 'rgba(179,212,255, 0.6)',
        background: 'transparent',
        color: theme.darkMode ? '#fff' : '#505f79',
        hoverBackground: theme.darkMode ? 'rgba(138,155,168,.15)' : 'rgba(9, 30, 66, 0.08)',
        hoverColor: theme.darkMode ? '#fff' : '#505f79',
      },
    },
    fontFamily:
      !theme.font || theme.font.length <= 0
        ? `-apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, ${fallbackFonts}`
        : `${theme.font},${fallbackFonts}`,
    isArabic,
    isDark: theme.darkMode,
    isMobile: isMobile(),

    input: {
      active: {
        border: mainColors.primaryColor,
      },
      background: theme.darkMode ? '#293742' : '#fff',
      border: theme.darkMode ? '#757575' : '#ced4d9',
      color: mainColors.textColor,
    },
    tags: {
      danger: '#ff5500',
      info: '#17a2b8',
      primary: mainColors.primaryColor,
      secondary: '#6c757d',
      success: '#87d068',
      warning: '#f58f29',
    },
  };
}

export default generateTheme;
