import * as React from 'react';

import Avatar from '@vuukle/avatar';
import { IDropDownItem } from '@vuukle/dropdown';
import Link from '@vuukle/link';
import CommentAction, { ICommentActionProps } from './components/CommentAction';
import DropdownMore from './components/DropdownMore';
import ShareCommentAction, { IShareActionProps } from './components/ShareCommentAction';
import { StyledComment } from './styles';

export interface IProps {
  // General Info
  name: string;
  avatar?: string;
  points: string | number | undefined;
  likes: number;
  dislikes: number;
  isModerator?: boolean;
  minimize?: boolean;
  comment: string | React.Component<any> | JSX.Element;
  date: string | Date;
  // Actions
  options?: IDropDownItem[];
  actions?: Array<React.ReactElement<any> | string | React.FunctionComponent<any>>;
  header?: string | React.Component<any> | Array<React.Component<any>> | JSX.Element | JSX.Element[] | null;
  className?: string;
  /**
   * The value used for the hash function.
   * The name is used as the hashValue by default.
   * When dealing with anonymous users you should use the id instead.
   * @default name
   */
  hash?: string;
  highlighted?: boolean;
  onUserClick: () => void;
}

export class Comment extends React.PureComponent<IProps, {}> {
  /** Use our TabItem component as child component for easier usage */
  public static Share = ShareCommentAction as React.FunctionComponent<IShareActionProps>;
  /** Use our TabItem component as child component for easier usage */
  public static Action = CommentAction as React.FunctionComponent<ICommentActionProps>;

  public static defaultProps = {
    className: '',
    isModerator: false,
    minimize: false,
  };

  public handleProfileClick = (e: React.MouseEvent<HTMLAnchorElement>) => {
    e.preventDefault();
    this.props.onUserClick();
  };

  public render() {
    return (
      <StyledComment className={`v-comment-item ${this.props.className}`} highlighted={this.props.highlighted}>
        <div className="v-comment__avatar">
          <Avatar
            name={this.props.name}
            src={this.props.avatar}
            hash={this.props.hash || this.props.name}
            onClick={this.handleProfileClick}
          />
        </div>
        <div className="v-comment__content">
          <div>
            <div className="v-comment__top">
              <div>
                <Link href="#no" className="v_comment__name" onClick={this.handleProfileClick}>
                  <strong>{this.props.name}</strong>
                </Link>
                {this.props.header}
                {this.props.points && <span className="points">{this.props.points}</span>}
                {this.props.date && <time>{this.props.date}</time>}
              </div>
              <div className="v-comment__top-right">
                {this.props.options && <DropdownMore options={this.props.options} />}
              </div>
            </div>
            {!this.props.minimize && <div className="v-comment__comment">{this.props.comment}</div>}
            {!this.props.minimize && (
              <div className="v-comment__actions">
                <div>{this.props.actions}</div>
                <div />
              </div>
            )}
          </div>
        </div>
      </StyledComment>
    );
  }
}

export default Comment;
