import Avatar from '@vuukle/avatar';
import styled from 'styled-components';

interface IProps {
  highlighted?: boolean;
  className?: string;
}

export const StyledComment = styled.div<IProps>`
  display: grid;
  position: relative;
  grid-template-rows: auto auto;
  grid-template-columns: auto 1fr;
  grid-template-areas: 'avatar-area comment-area' '. nested-comments-area';

  ${(props) => {
    if (props.highlighted) {
      if (props.theme.isArabic) {
        return `
          border-right: 2px solid #ffd34f;
          padding-right: 5px;
        `;
      } else {
        return `
          border-left: 2px solid #ffd34f;
          padding-left: 5px;
        `;
      }
    }
    return null;
  }}

  ${Avatar} {
    margin-right: 12px;
    grid-area: avatar-area / avatar-area / avatar-area / avatar-area;
  }

  /** @group comment top */
  .v-comment__top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;

    > div {
      flex-wrap: wrap;
      display: inline-flex;
      flex-direction: row;
      align-items: center;
    }

    .v_comment__name {
      word-break: break-word;
    }

    time,
    .points {
      font-size: 0.8em;
      &:before {
        content: '\\B7';
        display: inline-block;
        margin-left: 5px;
        margin-right: 5px;
      }
    }
  }

  .v-comment__content {
    min-width: 0px;
    word-wrap: break-word;
    grid-area: comment-area / comment-area / comment-area / comment-area;
  }

  .v-comment__comment {
    color: ${(props) => props.theme.textColor};
    margin: 0 0 10px;
  }

  .v-comment__actions {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 5px;
    > div {
      display: flex;
      align-items: center;
    }
  }

  .v-comment__action {
    align-items: center;
    align-self: center;
    margin: 0px;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: rgb(107, 119, 140);
    text-decoration: none;
    + .v-comment__action {
      &:before {
        color: rgb(66, 82, 110);
        content: '·';
        display: inline-block;
        text-align: center;
        width: 15px;
      }
    }
    button {
      &.active {
        background: #deebff;
      }
    }
  }
  .v-comment__top-right {
    opacity: ${(props) => (props.theme.isMobile ? 1 : 0)};
    transition: all 0.35s linear;
  }

  &:hover {
    .v-comment__top-right {
      opacity: 1;
    }
  }
`;
