import lighten from 'polished/lib/color/lighten';
import * as React from 'react';
import styled from 'styled-components';
import getIcon from './utils';

declare type SocialName =
  | 'disqus'
  | 'facebook'
  | 'linkedin'
  | 'twitter'
  | 'google'
  | 'whatsapp'
  | 'reddit'
  | 'messenger'
  | 'email'
  | 'pinterest'
  | 'flipboard'
  | 'telegram';

export interface IProps {
  /** Custom React Styles object */
  style?: object;
  /**
   * Button size in any css unit (px, rem, em etc.)
   * @default 32px
   */
  size?: string;
  /**
   * Size of icon inside button
   * @default 18px
   */
  iconSize?: string;
  /**
   * Disable button
   * @default false
   */
  disabled?: boolean;
  title?: string;
  /** onClick event */
  onClick: (e: React.MouseEvent<HTMLButtonElement>) => void;
  /** Type of social network */
  type: SocialName | 'link';

  className?: string;
}

const defaultProps: Partial<IProps> = {
  iconSize: '18px',
  size: '32px',
};

/**
 * @render react
 * @name SocialButton
 * @description  Social share button
 * @example
 * <SocialButton type="facebook" size="100px" onClick={(e) => console.log('Facebook social button clicked')} />
 */
export class SocialButton extends React.Component<IProps, {}> {
  public static defaultProps = defaultProps;

  constructor(props: IProps) {
    super(props);
  }

  public render() {
    const { type, iconSize, size, className, ...props } = this.props;

    return (
      <button className={`social-button social-button--${type} ${className}`} {...props}>
        {getIcon(this.props.type)}
      </button>
    );
  }
}

/** Styled SocialButton */
const colorsMap = {
  disqus: '#2E9FFF',
  email: '#048ee7',
  facebook: '#3b5998',
  flipboard: '#E12828',
  google: '#dc4e41',
  link: '#474e5e',
  linkedin: '#0077b5',
  messenger: '#0084ff',
  pinterest: '#BD081C',
  reddit: '#ff4500',
  telegram: '#2ca5e0',
  twitter: '#1da1f2',
  whatsapp: '#25d366',
};

const StyledSocialButton = styled(SocialButton)<IProps>`
  width: ${props => props.size || defaultProps.size};
  height: ${props => props.size || defaultProps.size};
  background: ${props => colorsMap[props.type] || colorsMap.facebook};

  border-radius: 50%;
  text-align: center;
  transition: all 0.2s linear;
  outline: none;
  vertical-align: middle;
  border: 0;
  cursor: pointer;
  padding: 2px;

  svg {
    max-width: ${props => props.iconSize || defaultProps.iconSize};
    max-height: ${props => props.iconSize || defaultProps.iconSize};
    width: 100%;

    fill: #fff;
    display: inline-block;
    vertical-align: middle;
  }

  @media (hover: hover) {
    &:hover {
      background: ${props => lighten(0.12, colorsMap[props.type] || colorsMap.facebook)};
    }
  }
`;

StyledSocialButton.defaultProps = defaultProps; // This is not working for inferno

export default StyledSocialButton;
