import getLuminance from 'polished/lib/color/getLuminance';
import * as React from 'react';
import styled from 'styled-components';

export interface IProps {
  children: any;
  /**
   * Bold text
   * @default false
   */
  bold?: boolean;
  /**
   * Badge type
   * @default primary
   */
  type?: 'primary' | 'secondary' | 'success' | 'danger' | 'warning' | 'info';
  /**
   * Pill type of badge (more rounded corners)
   * @default false
   */
  pill?: boolean;
  /**
   * Add class or classes to badge
   */
  className?: string;
  style?: object;
  theme?: any;
}

/**
 * @render react
 * @name Tag
 * @description Used to label or highlight content.
 * Usage examples: User points or Author type badge (Admin, Mod ...)
 * @example
 * <Tag type="primary">2000 points</Tag>
 */
const Tag: React.FunctionComponent<IProps> = ({
  bold = false,
  type = 'primary',
  children,
  className = '',
  pill = false,
  ...props
}) => (
  <span className={className} {...props}>
    {children}
  </span>
);

Tag.defaultProps = {
  theme: {
    tags: {
      danger: '#ff5500',
      info: '#17a2b8',
      primary: '#108ee9',
      secondary: '#6c757d',
      success: '#87d068',
      warning: '#ffc107',
    },
  },
};

const StyledTag = styled(Tag)<IProps>`
  display: inline-block;
  padding: 0.3em ${props => (props.pill ? '.6rem' : '.4rem')};
  border-radius: ${props => (props.pill ? '10rem' : '.3rem')};
  font-size: 75%;
  line-height: 0.8rem;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  ${props => {
    const color = (props.theme.tags && props.theme.tags[props.type || 'primary']) || '#108ee9';
    return `
      background: ${color};
      color: ${getLuminance(color) < 0.3 ? '#fff' : '#000'};
    `;
  }};
  user-select: none;
  font-weight: ${props => (props.bold ? '700' : '500')};
`;

export default StyledTag;
