import * as React from 'react';

const placeholderImg =
  // tslint:disable-next-line
  'data:image/svg+xml;base64,';

export interface IState {
  errorLoading: boolean;
}

export interface IProps {
  /**
   * Link to image
   * @default placeholderImage
   */
  src?: string;
  /**
   * Image description
   * @default ''
   */
  alt?: string;
  /**
   * Image size
   * @default 100%
   */
  size?: string;
}

/**
 * @render react
 * @name Image
 * @description  Component to load image which will handle errors on loading and show image placeholder
 * @example
 * <Image src="https://picsum.photos/100/100" size="100px" />
 */
export class Image extends React.Component<IProps, IState> {
  public static defaultProps: IProps = {
    alt: '',
    size: '100%',
    src: placeholderImg,
  };

  constructor(props: IProps) {
    super(props);
    this.state = {
      errorLoading: typeof props.src === 'string' && !props.src.match(/https?|base64|data:image/g),
    };

    if (!props.src) {
      props.src = placeholderImg;
    }
  }

  public render() {
    return (
      <img
        onError={() => this.setState({ errorLoading: false })}
        src={this.state.errorLoading ? placeholderImg : this.props.src}
        alt={this.props.alt}
        width={this.props.size}
        height={this.props.size}
        style={{ objectFit: 'cover' }}
      />
    );
  }
}

export default Image;
